//wallpaper
function createWallpaperPreview(canvasConfig, container, imageHostId, wallpaperPainter) {
    // canvasConfig canvs
    // containerdiv
    // imageHostId divimgid
    // wallpaperPainter 

    var canvas = createCanvas(canvasConfig, container)

    wallpaperPainter(canvas)

    var image = document.getElementById(imageHostId)

    image.src = canvas.toDataURL().replace("image/png", "image/octet-stream")
    
    container.removeChild(canvas)

    // canvas
    function createCanvas(canvasConfig, container) {
        if (typeof(canvasConfig.width) != 'number' || typeof(canvasConfig.height) != 'number' || canvasConfig.width <= 0 || canvasConfig.height <= 0) {
            throw ('canvasConfig wrong')
        }

        var width = canvasConfig.width
        var height = canvasConfig.height

        var canvas = document.createElement('canvas')
        container.appendChild(canvas)

        canvas.width = width
        canvas.height = height

        return canvas
    }
}

export default createWallpaperPreview



